/* STATIC STYLES (desktop-first) */

.timeline-container {
  position: relative;
  padding: 2rem 0;
  margin-top: 2rem;
}

.timeline-container::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 0;
  height: 100%;
  width: 4px;
  background: var(--color-primary-dark);
  transform: translateX(-50%);
  z-index: 1;
}

.timeline-line {
  position: absolute;
  left: 50%;
  top: 0;
  height: 100%;
  width: 4px;
  background: linear-gradient(to bottom, var(--color-primary-text), var(--color-secondary-dark));
  transform: translateX(-50%) scaleY(0);
  transform-origin: top;
  z-index: 2;
}

.timeline-item {
  display: flex;
  justify-content: center;
  gap: 3rem;
  position: relative;
  margin-bottom: 3rem;
  z-index: 3;
}

.timeline-node {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3.5rem;
  height: 3.5rem;
  background: var(--color-primary-darker);
  color: var(--color-secondary);
  border-radius: 50%;
  border: 1px solid var(--color-primary-dark);
  font-size: 1.8em;
}

.timeline-content {
  width: 20rem;
}

.timeline-description {
  width: 20rem;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
  .timeline-content,
  .timeline-description {
    width: 15rem;
  }
}

@media (max-width: 768px) {
  .timeline-container::before,
  .timeline-line {
    left: 3rem;
  }

  .timeline-item {
    display: grid;
    grid-template-columns: 6rem 1fr;
    grid-template-rows: auto auto;
    grid-template-areas:
      "node description"
      "node content";
    align-items: center;
    justify-items: center;
    margin-bottom: 2rem;
    position: relative;
    padding-left: 0;
    gap: 0;
  }

  .timeline-node {
    grid-area: node;
  }

  .timeline-description {
    grid-area: description;
    text-align: center;
  }

  .timeline-content {
    grid-area: content;
    width: 70%;
  }
}

@media (max-width: 576px) {
  .timeline-container::before,
  .timeline-line {
    left: 1rem;
  }

  .timeline-item {
    display: grid;
    grid-template-columns: 1rem 1fr;
  }

  .timeline-node {
    margin-left: 1rem;
  }

  .timeline-content,
  .timeline-description {
    width: 80%;
    margin-right: -2rem;
  }
}