/* STATIC STYLES (desktop-first) */

.portfolio-cards {
  column-count: 3;
  column-gap: 1.5rem;
}

.portfolio-cards > * {
  break-inside: avoid;
  margin-bottom: 1.5rem;
}

.portfolio-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 1rem;
  padding: 2rem;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {

}

@media (max-width: 992px) {
  .portfolio-cards {
    column-count: 2;
  }
}

@media (max-width: 768px) {

}

@media (max-width: 576px) {
  .portfolio-cards {
    column-count: 1;
  }
}