/* STATIC STYLES (desktop-first) */

.card4 {
  position: relative;
  border: 1px solid var(--color-primary-darker);
  border-radius: 10rem;
  overflow: hidden;
  aspect-ratio: 5/6;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  background-color: var(--color-primary-darkest);
  z-index: 40;
}

.card4:hover {
  transform: translateY(-5px);
}

.card4::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 10rem;
  border: 2px solid transparent;
  background: radial-gradient(circle, var(--color-primary-dark) 0%, var(--color-primary-darker) 100%);
  background-attachment: fixed;
  opacity: 0;
  transition: opacity 0.5s ease;
}

.card4:hover::before {
  opacity: 1;
}

.card4-background {
  position: absolute;
  left: 0;
  bottom: -100%;
  width: 100%;
  height: 100%;
  background-color: var(--color-primary-dark);
  transition: bottom 0.7s ease;
}

.card4:hover .card4-background {
  bottom: 0;
}

